import React from 'react';
import styles from "styles/Footer.module.css";

const Footer = ({ t }) => {
    return (
        <div className={styles.footerWrap}>
            <div className={styles.footerNotice}>
                <p>        ,             .</p>
            </div>
            <footer>
                <ul className={styles.footerInfo}>
                    <li>
                        <a href={"/customerService"}></a>
                        <button onClick={() => {window.open('/faq/policy/TermsOfService', '_blank')}}></button>
                        <button onClick={() => {window.open('/faq/policy/PrivacyPolicy', '_blank')}}>
                            <strong></strong>
                        </button>
                    </li>
                    <li>
                        <button onClick={() => window.open('https://www.qpicker.com/about')}>  </button>
                        <span> </span>
                    </li>
                </ul>
                <ul className={styles.footerAcc}>
                    <li> ()</li>
                    <li>:</li>
                    <li>:</li>
                    <li>:890-86-00557</li>
                    <li>:2019--0295</li>
                    <li>  1 12-9, 3</li>
                    <li>:suport@peopulley.com</li>
                </ul>
                <span className={styles.copyright}>Copyright  peopulley Inc. All rights reserved.</span>
                <div className={styles.footerSns}>
                    <a target={"_blank"} rel="noreferrer" href={"https://www.instagram.com/qpicker/"} className={styles.insta}> </a>
                    <a target={"_blank"} rel="noreferrer" href={"https://www.youtube.com/channel/UC7t7AkvgfAGrk6aMfLuYJEA?app=desktop"} className={styles.youtube}> </a>
                </div>
            </footer>
        </div>
    );
};

export default Footer;